class book {
    constructor(title, author, cover) {
        this.title = title;
        this.author = author;
        this.cover = cover;
    }
    //M√©thode qui permet de cr√©er les cartes de Livres
    display() {
        //V√©rification de la r√©cup√©ration des valeurs
        console.log(this.title + " by " + this.author + " with " + this.cover);
        //Je cr√©e le HTML qui permet d'afficher la carte du livre avec un innerHTML situ√© dans la div bookContainer
        const bookContainer = document.getElementById('bookContainer');
        bookContainer.innerHTML += `
        <div class="col-md-3">
            <div class="card " style="width: 15rem;">
                <img src="`+ this.cover +`" class="card-img-top" alt="Film cover picture">
                <div class="card-body">
                    <h5 class="card-title">`+ this.title +`</h5>
                    <p class="card-text">`+ this.author.toUpperCase() +`</p>
                </div>
            </div>
        </div>`
    }
}

//R√©cup√©ration des donn√©es du formulaire
const form = document.querySelector('form');

//Cr√©ation d'un nouvel objet book √† partir des donn√©es du formulaire
form.addEventListener('submit', (e) => {
    //On bloque le rafraichissement auto
    e.preventDefault();
    //V√©rification de la r√©cup√©ration des valeurs
    console.log(e.target.inputName.value);
    console.log(e.target.inputAuthor.value);
    console.log(e.target.inputCover.value);

    //Cr√©ation d'un nouvel objet book
    let newBook = new book(e.target.inputName.value, e.target.inputAuthor.value, e.target.inputCover.value);
    newBook.display();
    form.reset();
    //Animation de la carte : si on clique sur la carte le texte se r√©tracte ou se remet en place.
    $(document).ready(function () {
        $('.card').click(() => {
            $('.card-body').toggle();
        });
        
    });

});

$(document).ready(function () {
    let heading = $("#pulse");
    heading.fadeIn(2000);
});
